<script>
import anime from 'animejs/lib/anime.es.js';
import { MathUtils } from 'three';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import _ from 'lodash';

const lerp = MathUtils.lerp;

export default {
  name: 'RoadmapSVG',
  mounted() {
    if (
      /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
        navigator.userAgent
      )
    ) {
      var mySVG = document.getElementById('svgElement');
      mySVG.setAttribute('viewBox', '0 0 2850 346');
    }

    function calculateScrollPercentage() {
      const svgTest = document.getElementById('svgElement');
      const svgWidth = svgTest.getBoundingClientRect().width;

      const ww = window.innerWidth;

      return ((svgWidth - ww) / svgWidth) * 100;
    }

    gsap.registerPlugin(ScrollTrigger);

    gsap.to('#svgElement', {
      xPercent: () => -calculateScrollPercentage(), // Edit this to decide how far it will scroll before going vertically again
      ease: 'none',
      scrollTrigger: {
        trigger: '#roadmapSection',
        start: 'top top',
        end: '+5000',
        scrub: true,
        pin: true,
        invalidateOnRefresh: true,
        anticipatePin: 1,
        onUpdate: ({ progress }) => animateYellowLine(progress),
      },
    });

    const TIMELINE_DURATION = 1000;

    function animateYellowLine(progress) {
      scrollProgress = progress;
    }

    const tl = anime.timeline({
      easing: function() {
        return function(t) {
          return Math.pow(t, 1.01);
        };
      },
      duration: TIMELINE_DURATION,
      autoplay: false,
    });

    tl.add({
      targets: '#clipRect',
      width: 3000,
      duration: 1000,
    });

    let scrollProgress = 0;
    let animationPercentage = 0;

    const render = _.debounce(function() {
      requestAnimationFrame(render);

      animationPercentage = lerp(animationPercentage, scrollProgress, 0.1);
      tl.seek(animationPercentage * TIMELINE_DURATION);
    }, 10);

    render();

    window.addEventListener('resize', calculateScrollPercentage);
  },
};
</script>

<style scoped lang="scss">
svg {
  height: 100%;
  overflow: hidden;
}

#roadmap-line {
  opacity: 1;
  mask: url(#clip);
}

.roadmapItem {
  @media only screen and (max-width: 800px) {
    transform: translateX(-400px);
  }
}

#titleText
 {
  @media only screen and (max-width: 800px) {
    transform: scale(0.4);
  }
}
</style>
